*,
*::before,
*::after {
    box-sizing: border-box;
}

* {
    margin: 0;
}

body {
    line-height: 1.5;
    -webkit-font-smoothing: antialiased;
}

img,
picture,
video,
canvas,
svg {
    display: block;
    max-width: 100%;
}

input,
button,
textarea,
select {
    font: inherit;
}

p,
h1,
h2,
h3,
h4,
h5,
h6 {
    overflow-wrap: break-word;
}

p {
    text-wrap: pretty;
}
h1,
h2,
h3,
h4,
h5,
h6 {
    text-wrap: balance;
}

#root,
#__next {
    isolation: isolate;
}
/* Reset Over */
/* General Styling */
:root {
    --blue-one: #eff9ff;
    --blue-two: #e0f2fe;
    --blue-three: #b8e6ff;
    --blue-four: #73d4ff;
    --blue-five: #00bcff;
    --blue-six: #00a6f5;
    --blue-seven: #0084d1;
    --blue-eight: #0069a8;
    --blue-nine: #00598a;
    --blue-ten: #004a70;
    --blue-eleven: #062f4a;
    --font-header: "Fredoka", -apple-system, "Inter", Arial, Helvetica,
        sans-serif;
    --font-general: -apple-system, "Inter", Arial, Helvetica, sans-serif;
}

body {
    display: flex;
    flex-direction: column;
    align-items: center;
    font-family: var(--font-general);
}

/* App Styling */
.app-container {
    width: 40rem;
    min-height: 14rem;
    margin-top: 3rem;
    margin-bottom: 2rem;
    padding: 2rem;
    border-radius: 20px;
    background-color: var(--blue-ten);
    box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.5);
}

header {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 1rem;
}

.header-icon {
    margin-left: -2rem;
}

header > h1 {
    font-family: var(--font-header);
    font-weight: 500;
    color: white;
}

main {
    margin-top: 2rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 2rem;
}

/* Form Section */
.form-section {
    display: flex;
    align-items: center;
}

#search {
    padding-left: 1rem;
    padding-right: 1rem;
    border-radius: 16px;
    border: none;
    height: 2rem;
    width: 16rem;
    font-weight: 600;
}

#search:focus {
    outline: none;
}

.toggle-temp-format {
    margin-left: 10px;
}

.switch {
    position: relative;
    display: inline-block;
    width: 50px;
    height: 25px;
}

.switch input {
    opacity: 0;
    width: 0;
    height: 0;
}

.slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: red;
    transition: 0.4s;
    border-radius: 25px;
}

.slider:before {
    position: absolute;
    content: "";
    height: 18px;
    width: 18px;
    left: 4px;
    bottom: 3px;
    background-color: white;
    transition: 0.4s;
    border-radius: 50%;
}

input:checked + .slider {
    background-color: #4caf50;
}

input:checked + .slider:before {
    transform: translateX(24px);
}

.slider.round {
    border-radius: 25px;
}

.slider.round:before {
    border-radius: 50%;
}

#unitLabel {
    font-size: 18px;
    font-weight: bold;
}

/* Current Weather */
.current-weather {
    width: 34rem;
    min-height: 2rem;
    display: grid;
    color: white;
    align-items: center;
    font-weight: 600;
}

#loading-container {
    justify-self: center;
}

.giphy-embed {
    border-radius: 25px;
}

.current-weather-error {
    justify-self: center;
}

.grid-on {
    grid-template-rows: repeat(6, 2.5rem);
    grid-template-columns: 7.5rem repeat(3, 1fr);
}

.current-icon-container {
    grid-area: 1 / 1 / 4 / 2;
    justify-self: center;
    width: 100%;
    height: 100%;
    padding-right: 10px;
    padding-bottom: 20px;
}

.current-icon {
    width: 100%;
    height: auto;
}

.location-container {
    grid-area: 1 / 2 / 2 / -1;
    font-weight: 700;
}

.temp-container {
    grid-area: 2 / 2 / 3 / 3;
    font-size: 2rem;
    font-weight: 700;
}

.feels-like-container {
    grid-area: 2 / 3 / 3 / -1;
}

.humidity-container {
    grid-area: 3 / 2 / 3 / 3;
}

.precip-container {
    grid-area: 3 / 3 / 3 / 4;
}

.uvindex-container {
    grid-area: 3 / 4 / 4 / -1;
}

.conditions-container {
    grid-area: 4 / 1 / 5 / -1;
}

.description-container {
    grid-area: 5 / 1 / 6 / -1;
    line-height: 1.2;
}

.sunrise-container {
    grid-area: 6 / 1 / -1 / 3;
}

.sunset-container {
    grid-area: 6 / 3 / -1 / -1;
}

.hourly-weather {
    min-width: 34rem;
    min-height: 2rem;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    font-weight: 500;
}

.hidden {
    display: none;
}

#left-button {
    margin-right: 1.5rem;
    transition: scale 0.3s ease;
}

#right-button {
    margin-left: 1.5rem;
    transition: scale 0.3s ease;
}

#left-button:hover,
#right-button:hover {
    scale: 1.2;
}

.carousel-button {
    width: 6rem;
    background-color: inherit;
    border: none;
    cursor: pointer;
}

.arrow-icon {
    background-color: inherit;
}

.carousel-frame {
    width: 20rem;
    height: 10rem;
    overflow: hidden;
    position: relative;
}

.carousel {
    display: flex;
    overflow: hidden;
    position: relative;
    width: 160rem;
    right: 0rem;
}

.hour-container {
    width: 6.66rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    color: white;
    font-weight: 600;
}

.hour-icon {
    width: 6.66rem;
    height: 6rem;
}

/* Footer */
footer {
    display: flex;
    justify-content: center;
    align-items: center;
    padding-bottom: 2rem;
    font-weight: 500;
}

.credit {
    display: flex;
    gap: 0.3rem;
}

footer a {
    text-decoration: none;
    color: var(--blue-ten);
    transition: color 0.3s ease;
}

footer a:hover {
    color: var(--blue-five);
}

.credit img {
    height: 1.2rem;
    width: 1.2rem;
}
